<template>
  <div class="context">
    <div class="content">
      <div class="table-container">
        <table id="grid-basic" class="filterable-table bootgrid-table">
          <thead>
          <tr>
            <th data-column-id="id" data-type="numeric">ID</th>
            <th data-column-id="avatar">Фото</th>
            <th data-column-id="email">Логин</th>
            <th data-column-id="name">Имя</th>
            <th data-column-id="surname">Фамилия</th>
            <th data-column-id="gifts">Подарки</th>
            <th data-column-id="register-date">Дата регистрации</th>
          </tr>
          </thead>
          <tbody id="grid-basic-result">
          <tr v-for="lady in ladies" v-on:click="openTalkyTimeLady(lady)">
            <td>{{ lady.id }}</td>
            <td><img :src="lady.avatarOriginal" alt="" class="round"/></td>
            <td>{{ lady.email }}</td>
            <td>{{ lady.name || "-" }}</td>
            <td>{{ lady.lastname || "-" }}</td>
            <td>{{ Boolean(lady.canReceiveGift) ? "принимает" : "не принимает" }}</td>
            <td>{{ lady.dateCreated }}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import '@/external/jquery.dataTables.min.css'
import '@/external/jquery.dataTables.min.js'
import Connection from "@/rsocket/connection";
import Vue from "vue";
import router from "@/views/router/routes";

export default {
  name: "TalkyTimeList",
  methods: {
    openTalkyTimeLady(lady) {
      router.push("/dashboard/talkytime/lady/" + lady.id)
    }
  },
  mounted() {
    Connection.talkyTimeLadyList((res) => {
      let data = JSON.parse(res.data.toString());
      this.ladies = data.ladies;
    }).then(() => {
      $('#grid-basic').DataTable();
    });
  },
  data: () => {
    return {
      ladies: []
    }
  }
}
</script>

<style lang="scss" scoped>
.content {
  box-sizing: border-box;
  background: #222427;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  color: white;
  text-align: center;
}

.table-container {
  background: #2c2f33;
  color: #fff;
}

.filterable-table {
  overflow: hidden;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  text-align: center;
  background-color: #494e55;
  font-weight: bold;

  tbody tr {
    border-bottom: 2px solid #0000006c;
  }
}

@media screen and (max-width: 768px) {
  table.filterable-table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }

  table.filterable-table tr {
    border-bottom: 1px solid #ddd;
    display: block;
    margin-bottom: 30px;
  }

  table.filterable-table td {
    display: block;
    text-align: right;
  }

  table.filterable-table td::before {
    content: attr(data-label);
    float: left;
    font-weight: bold;
    text-transform: uppercase;
  }

  table.filterable-table td:last-child {
    border-bottom: 0;
  }
}

.round {
  width: 64px;
  height: 64px;
  border-radius: 50%;
}
</style>